body {
  position: relative;
}

h1, h2, p {
  margin-top: 0;
  margin-bottom: 10px;
}

img {
  max-width: 400px;
  height: auto;
  display: block;
}

p {
  font-size: 18px;
}

.loader-container {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader {
  perspective: 1000px;
  width: 48px;
  height: 48px;
  color: #fff;
  border-radius: 50%;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader:after {
  color: #ff3d00;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .2em 0;
  }

  12% {
    box-shadow: .2em .2em;
  }

  25% {
    box-shadow: 0 .2em;
  }

  37% {
    box-shadow: -.2em .2em;
  }

  50% {
    box-shadow: -.2em 0;
  }

  62% {
    box-shadow: -.2em -.2em;
  }

  75% {
    box-shadow: 0 -.2em;
  }

  87% {
    box-shadow: .2em -.2em;
  }
}

.select-wrapper {
  justify-content: center;
  display: flex;
}

.breed-select {
  width: 500px;
}

.cat-info {
  justify-content: center;
  gap: 20px;
  margin-top: 40px;
  display: flex;
}

.cat-desc {
  width: 500px;
}

.is-hidden {
  display: none;
}

